.GameIcon-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
}

.GameIcon {
  padding: 10px;
  margin: 15px;
  border-radius: 20px;
  -webkit-box-shadow: 10px 10px 30px rgba(0,0,0,.2);
          box-shadow: 10px 10px 30px rgba(0,0,0,.2);
  -webkit-transition: .1s ease;
  -o-transition: .1s ease;
  transition: .1s ease;
}

.GameIcon:hover {
  background-color: #fafafa;
  -webkit-box-shadow: 8px 8px 25px rgba(0,0,0,.2);
          box-shadow: 8px 8px 25px rgba(0,0,0,.2);
}

.GameIcon p {
  font-size: .8em;
  color: #2d3e50;
}

.board.icon td {
  width: 15px;
  height: 15px;
  /* border: 1px solid #95A5A5; */
  background-color: #ecf0f1;
  font-size: .6em;
}

.board.icon td.empty {
  -webkit-box-shadow: inset -.5px -.5px 0px 0px #95a5a5, inset .5px .5px 0px 0px #95a5a5;
          box-shadow: inset -.5px -.5px 0px 0px #95a5a5, inset .5px .5px 0px 0px #95a5a5
}

.board.icon td.white {
  background-color: #bec3c7;
}

.board.icon td.black {
  background-color: #2d3e50;
  color: #ecf0f1;
}

.GameIcon {
  cursor: pointer;
}



