


.piece {
  height: 50px;
  width: 50px;
  line-height: 50px;
  -webkit-transition: .05s linear;
  -o-transition: .05s linear;
  transition: .05s linear;
}

.piece span {
  cursor: pointer;
  -webkit-transition: .1s linear;
  -o-transition: .1s linear;
  transition: .1s linear;
  border-radius: 50%;
}

.piece span:hover {
  background-color: rgba(0,0,0,.2);
}

.piece.active {
  color: #1bbc9b;
}

.virtual-container {
  position: absolute;
  width: 25px;
  height: 25px;
}

.virtual {
  position: relative;
  top: 0;
  left: 0;
  width: calc(50px * 8);
  height: calc(50px * 8);
}

.virtual-col {

}

.virtual-row {
  width: 50px;
  height: 50px;
}

.virtual-row .virtual-row-square {
  height: 100%;
  width: 100%;
  background-color: rgba(45, 204, 112, 0.4);
  /* box-shadow: inset -1px -1px 0px 0px black, inset 1px 1px 0px 0px black; */
  cursor: pointer;
  -webkit-transition: .1s linear;
  -o-transition: .1s linear;
  transition: .1s linear;
}

.virtual-row .virtual-row-square:hover {
  background-color: rgba(45, 204, 112, 0.85);
}


.virtual-row .virtual-row-square.empty {
  background-color: rgba(255,255,255,0);
  cursor: default;
}


.virtual-row .virtual-row-square.enemy {
  background-color: rgba(232,76,61,.4);
}
.virtual-row .virtual-row-square.enemy:hover {
  background-color: rgba(232,76,61,.7);
}


.virtual-row .virtual-row-square.friend {
  background-color: rgba(230,127,34,.4);
  cursor: default;
}
.virtual-row .virtual-row-square.friend:hover {
  background-color: rgba(230,127,34,.7);
}


.virtual-row .virtual-row-square.obstruct {
  background-color: rgba(50,50,250, 0);
  cursor: default;
}
.virtual-row .virtual-row-square.obstruct:hover {
  background-color: rgba(50,50,250, 0);
}



