
.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.container .board-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.container > div, .container .board, .container .playerUI {
  -ms-flex: 1 1;
      flex: 1 1;
}

.board {
  width: 100%;
  /* height: 100vh; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  /* overflow: hidden; */
}

table, tbody, tr, td {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-collapse: collapse;
}

td {
  width: 50px;
  height: 50px;
  font-size: 30px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

@media only screen and (max-width: 764px) {
  td {
    width: 25px;
    height: 25px;
    font-size: 20px;
  }
  .container .board-container {
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
  }
  .board {
    width: calc(20px * 8);
  }
}



